                                    <label class="col-lg-5 fw-bold text-uppercase">Kategori</label>
                                    <div class="col-md-7">
                                        <Dropdown filter showClear v-model="form.photo_category_id" :options="props.photo_categories" optionValue="id" optionLabel="name" class="w-100"/>
                                    </div>
