                                        <div class="col-md-7">
                                            <Dropdown filter showClear v-model="form.photo_sub_category_id" :options="props.photo_sub_categories.filter((x) => x.photo_category_id === form.photo_category_id)" optionValue="id" optionLabel="name" class="w-100"/>
                                        </div>
                                    </div>
                                    <Divider />
                                </div>

                                <div v-if="form.photo_category_id && form.photo_sub_category_id">
                                    <div class="row align-items-center mb-5">
                                        <label class="col-lg-5 fw-bold text-uppercase">Aile</label>
                                        <div class="col-md-7">
                                            <Dropdown filter showClear v-model="form.photo_family_id" :options="props.photo_families.filter((x) => x.photo_sub_category_id === form.photo_sub_category_id)" optionValue="id" optionLabel="name" class="w-100"/>
                                        </div>
                                    </div>
                                    <Divider />
                                </div>

                                <div v-if="form.photo_category_id && form.photo_sub_category_id && form.photo_family_id">
                                    <div class="row align-items-center mb-5">
                                        <label class="col-lg-5 fw-bold text-uppercase">Tür</label>
                                        <div class="col-md-7">
                                            <Dropdown filter showClear v-model="form.photo_type_id" :options="props.photo_types.filter((x) => x.photo_family_id === form.photo_family_id)" optionValue="id" optionLabel="name" class="w-100"/>
                                        </div>
                                    </div>
                                    <Divider />
                                </div>

                                <div class="row align-items-center mt-5">
                                    <label class="col-lg-5 fw-bold text-uppercase">Derinlik</label>
                                    <div class="col-md-7">
                                        <InputGroup>
                                            <InputGroupAddon>
                                                <span>m</span>
                                            </InputGroupAddon>
                                            <InputNumber class="w-100" v-model="form.depth" inputId="photoDepth" :useGrouping="false" :class="{'p-invalid':form.errors.depth}"/>
                                        </InputGroup>
                                        <small class="text-danger" v-if="form.errors.depth">Lütfen derinlik değerini giriniz.</small>
                                    </div>
                                </div>

                                <Divider />

                                <div class="row align-items-center mt-5">
                                    <label class="col-lg-5 fw-bold text-uppercase">Sıcaklık</label>
                                    <div class="col-md-7">
                                        <InputGroup>
                                            <InputGroupAddon>
                                                <span>°C</span>
                                            </InputGroupAddon>
                                            <InputNumber class="w-100" v-model="form.heat" inputId="photoHeat" :useGrouping="false" :class="{'p-invalid':form.errors.heat}"/>
                                        </InputGroup>
                                        <small class="text-danger" v-if="form.errors.heat">Lütfen sıcaklık değerini giriniz.</small>
                                    </div>
                                </div>

                                <div v-if="props.photo_attributes">
                                    <div v-for="(attribute, index) in form.attributes" :key="`photo-attribute-${index}`">
                                        <Divider />
                                        <div class="row align-items-center">
                                            <label class="col-lg-5 fw-bold text-uppercase">{{ attribute.name }}</label>
                                            <div class="col-md-7">

                                                <InputMask mask="99:99" v-if="attribute.option_type == 'time'" v-model="attribute.value" :class="{'p-invalid':form.errors[`attributes.${index}.value`]}"/>

                                                <InputMask mask="99.99.9999" placeholder="GG.AA.YYYY" v-if="attribute.option_type == 'date'" v-model="attribute.value" :class="{'p-invalid':form.errors[`attributes.${index}.value`]}"/>

                                                <InputMask mask="99.99.9999 99:99" placeholder="GG.AA.YYYY HH:SS" v-if="attribute.option_type == 'datetime'" v-model="attribute.value" :class="{'p-invalid':form.errors[`attributes.${index}.value`]}"/>

                                                <Dropdown filter showClear v-if="attribute.option_type == 'select'" v-model="attribute.value" :options="attribute.values" optionLabel="name" optionValue="name" placeholder="Lütfen seçiniz" class="w-100" :class="{'p-invalid':form.errors[`attributes.${index}.value`]}"/>

                                                <MultiSelect filter showClear v-if="attribute.option_type == 'multiselect'" v-model="attribute.value" :options="attribute.values" optionLabel="name" optionValue="name" placeholder="Lütfen seçiniz" :class="{'p-invalid':form.errors[`attributes.${index}.value`]}"/>

                                                <div class="d-flex flex-row flex-wrap align-items-center" v-if="attribute.option_type == 'image'">
                                                    <img :src="attribute.value" v-if="attribute.value" class="img-thumbnail" width="100"/>
                                                    <div class="d-flex flex-row align-items-center">
                                                        <PopupMediaLibrary v-if="!attribute.value" :on-select="setAttributeMedia" :form-model="attribute" :button-text="'Seç'" :mime-type="'image/'" :key="Math.floor(Math.random() * 100000)"/>
                                                        <button type="button" class="btn btn-sm btn-light-danger btn-icon ms-2" v-if="attribute.value" @click="attribute.value = null">
                                                            <i class="bi bi-x fs-3"></i>
                                                        </button>
                                                    </div>
                                                </div>

                                                <OptionType :option="attribute" v-model="attribute.value" v-else :error-class="form.errors[`attributes.${index}.value`] ? true : false"/>

                                                <small class="text-danger" v-if="form.errors[`attributes.${index}.value`]">{{ `${attribute.name} boş bırakılamaz.` }}</small>
                                            </div>
                                        </div>
                                    </div>
                                </div>

                                <Divider />

                                <div class="row align-items-center mt-5">
                                    <label class="col-lg-5 fw-bold text-uppercase">Detaylar</label>
                                    <div class="col-md-7">
                                        <Textarea autoResize rows="3" cols="30" class="w-100" v-model="form.summary"/>
                                    </div>
                                </div>

                                <Divider />

                                <div class="row align-items-center mt-5">
                                    <label class="col-lg-5 fw-bold text-uppercase">Konum Bilgileri</label>
                                    <div class="col-md-7">
                                        <div class="d-flex align-items-center gap-3">
                                            <div class="d-flex flex-grow-1">
                                                <input type="text" ref="autocompleteInput" placeholder="Adres giriniz..." class="p-inputtext p-component w-100" :class="{'p-invalid':form.errors.latitude}"/>
                                            </div>
                                            <button type="button" @click="useCurrentLocation" v-tooltip.top="'Mevcut Konumumu Kullan'" class="btn btn-icon btn-light-danger"><i class="bi bi-crosshair"></i></button>
                                        </div>
                                        <div v-show="mapVisible" class="h-300px w-100 mt-5" ref="mapContainer"></div>
                                        <small class="text-danger" v-if="form.errors.latitude">Lütfen konum bilgilerini giriniz.</small>
                                    </div>
                                </div>

                            </div>
                        </div>
                    </div>
                    <div class="col-xl-3">
                        <div class="card mb-5 mb-xl-10">
                            <div class="card-body">

                                <h5 class="card-title" :class="{'text-muted':!form.errors.mainImage,'text-danger':form.errors.mainImage}">Ana Görsel</h5>
                                <Divider class="mb-5"/>
                                <div class="mb-5 mb-xl-10">
                                    <div class="overlay overflow-hidden rounded" v-if="form.mainImage">
                                        <div class="overlay-wrapper rounded">
                                            <img :src="form.mainImage.original_url" class="img-fluid rounded" />
                                        </div>
                                        <div class="overlay-layer bg-dark bg-opacity-25">
                                            <button type="button" class="btn btn-sm btn-light-danger btn-icon ms-2" v-if="form.mainImage" @click="form.mainImage = null">
                                                <i class="bi bi-x fs-3"></i>
                                            </button>
                                        </div>
                                    </div>
                                    <PopupMediaLibrary v-if="!form.mainImage" :on-select="setMainImage" :button-text="'Seç'" :mime-type="'image/'" :key="Math.floor(Math.random() * 100000)"/>
                                    <small class="text-danger" v-if="form.errors.mainImage">Lütfen ana görseli seçiniz.</small>
                                </div>

                                <h5 class="card-title text-muted">Diğer Görseller</h5>
                                <Divider class="mb-5"/>
                                <div class="mb-5 mb-xl-10">
                                    <div v-if="form.galleryImages">
                                        <div class="row g-3 row-cols-3 mb-5">
                                            <div class="col" v-for="(media,m) in form.galleryImages" :key="m">
                                                <div class="overlay overflow-hidden rounded">
                                                    <div class="overlay-wrapper">
                                                        <img :src="media.preview_url" class="img-fluid rounded cursor-pointer" />
                                                    </div>
                                                    <div class="overlay-layer bg-dark bg-opacity-25">
                                                        <button type="button" class="btn btn-light-danger btn-sm btn-icon" @click="removeFromGallery(m)"><i class="bi bi-x fs-3"></i></button>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <PopupMediaLibrary :on-select="setGalleryImages" :multiple="true" :button-text="'Görselleri Seç'" :mime-type="'image/'" :key="Math.floor(Math.random() * 100000)"/>
                                </div>

                                <h5 class="card-title text-muted">Youtube Video</h5>
                                <Divider class="mb-5"/>
                                <div class="mb-5 pb-5 border-bottom hstack gap-2" v-if="form.youtube_id">
                                    <button type="button" class="btn btn-sm btn-light-danger btn-icon me-2" v-if="form.youtube_id" @click="form.youtube_id = null">
                                        <i class="bi bi-x fs-3"></i>
                                    </button>
                                    <a class="btn btn-sm btn-light-primary btn-icon" target="_blank" :href="form.youtube_id">
                                        <i class="bi bi-eye fs-3"></i>
                                    </a>
                                </div>
                                <div class="vstack gap-1">
                                    <InputText v-model="form.youtube_id" class="w-100"/>
                                </div>

                            </div>
                        </div>

                        <button type="submit" class="btn btn-primary" :class="{ 'opacity-25': form.processing || !form.isDirty }" :disabled="form.processing || !form.isDirty">Kaydet</button>
                    </div>
                </div>

            </form>
        </div>
    </div>
    
</template>

<script setup>

import {ref,shallowRef,onMounted, nextTick} from "vue";
import { useForm, usePage } from "@inertiajs/vue3";
import Divider from "primevue/divider";
import InputText from "primevue/inputtext";
import MultiSelect from "primevue/multiselect";
import Dropdown from "primevue/dropdown";
import Calendar from "primevue/calendar";
import PopupMediaLibrary from "../UserMediaLibrary/PopupMediaLibrary";
import OptionType from "../../Components/OptionType";
import InputMask from "primevue/inputmask";
import InputNumber from "primevue/inputnumber";
import InputGroup from 'primevue/inputgroup';
import InputGroupAddon from 'primevue/inputgroupaddon';
import Textarea from "primevue/textarea";

import { Fancybox } from "@fancyapps/ui";
import "@fancyapps/ui/dist/fancybox/fancybox.css";

const props = defineProps({
    photo_categories:Object,
    photo_sub_categories:Object,
    photo_families:Object,
    photo_types:Object,
    photo_attributes:Object,
});

const editor = ref(null);

let curr_language = usePage().props.current_language;
let google_api_key = usePage().props.google_api_key;

const autocompleteInput = ref(null);
const mapContainer = ref(null);
const map = shallowRef(null);
const marker = ref(null);
const mapVisible = ref(false);
const placeDetails = ref(null);

const form = useForm({
    language:curr_language,
    name:"",
    summary:"",
    shooting_date: null,
    photo_category_id:null,
    photo_sub_category_id:null,
    photo_family_id:null,
    photo_type_id:null,
    attributes:props.photo_attributes,
    depth:null,
    heat:null,
    latitude:null,
    longitude:null,
    country:"",
    city:"",
    county:"",
    address:"",
    mainImage:null,
    galleryImages:[],
    youtube_id:null
});

const create = () => {
    form.post(route('photos.store'),{
        onSuccess: () => {
            
        },
    });
}

const setMainImage = (media) => {
    form.mainImage = media;
}

const setGalleryImages = (medias) => {
    form.galleryImages = [...form.galleryImages, ...medias];
}

const removeFromGallery = (index) => {
    form.galleryImages.splice(index,1);
}


const updateFormGoogleFields = () => {

    form.latitude = placeDetails.value.geometry.location.lat();
    form.longitude = placeDetails.value.geometry.location.lng();

    placeDetails.value.address_components.forEach(component => {
        if (component.types.includes("country")) {
            form.country = component.long_name;
        }
        if (component.types.includes("administrative_area_level_1")) {
            form.city = component.long_name;
        }
        if (component.types.includes("administrative_area_level_2")) {
            form.county = component.long_name;
        }
    });

    form.address = placeDetails.value.formatted_address;

}

const initializeAutocomplete = () => {
    const input = autocompleteInput.value;
    const autocomplete = new google.maps.places.Autocomplete(input, {
        componentRestrictions: { country: "tr" }, // Sadece Türkiye
        fields: ["geometry", "formatted_address", "address_components"],
    });

    autocomplete.addListener("place_changed", () => {
    const place = autocomplete.getPlace();
    if (place.geometry) {
        const { lat, lng } = place.geometry.location;
        updateMap(lat(), lng());
        placeDetails.value = place;
        updateFormGoogleFields();
        console.log("Seçilen yer detayları:", place);
    }
    });
};

const updateMap = async (lat, lng) => {
    // DOM'un hazır olduğundan emin olun
    await nextTick();

    if (!map.value) {
        // Harita oluşturma
        map.value = new google.maps.Map(mapContainer.value, {
            center: { lat, lng },
            zoom: 13,
            panControl: false,
            mapTypeControl: false,
            scaleControl: false,
            streetViewControl: false,
            rotateControl: false,
            mapId:'24a6df6e5eacbfee'
        });

        // Marker oluşturma

        const { AdvancedMarkerElement } = await google.maps.importLibrary("marker");
        marker.value = new AdvancedMarkerElement({
            position: { lat, lng },
            map: map.value,
            gmpDraggable: true,
        });

        // Marker taşındığında olay
        marker.value.addListener("dragend", (event) => {
            const newLat = event.latLng.lat();
            const newLng = event.latLng.lng();
            console.log("Yeni koordinatlar:", { lat: newLat, lng: newLng });

            // Yeni konum detaylarını al
            const geocoder = new google.maps.Geocoder();
            geocoder.geocode({ location: { lat: newLat, lng: newLng } }, (results, status) => {
                if (status === "OK" && results[0]) {
                    console.log("Yeni yer detayları:", results[0]);
                    placeDetails.value = results[0];
                    updateFormGoogleFields();
                    autocompleteInput.value.value = results[0].formatted_address
                }
            });
        });

    } else {
        // Harita ve marker güncelleme
        map.value.setCenter({ lat, lng });
        marker.value.position = { lat, lng }; // Yeni pozisyonu güncelle
    }

    mapVisible.value = true;
};

const useCurrentLocation = () => {
    if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition((position) => {
            const { latitude, longitude } = position.coords;
                updateMap(latitude, longitude);
                console.log("Mevcut konum:", { lat: latitude, lng: longitude });
                // Adresi al ve input'a yaz
                const geocoder = new google.maps.Geocoder();
                geocoder.geocode({ location: { lat: latitude, lng: longitude } }, (results, status) => {
                    if (status === "OK" && results[0]) {
                        console.log("Mevcut konum adresi:", results[0]);
                        placeDetails.value = results[0];
                        updateFormGoogleFields();
                        autocompleteInput.value.value = results[0].formatted_address; // Input alanına adres yaz
                    }
                });
            },
            (error) => {
                console.error("Konum alınamadı:", error);
            }
        );
    } else {
        alert("Tarayıcınız konum servisini desteklemiyor.");
    }
};

const loadGoogleMapsAPI = () => {
    return new Promise((resolve, reject) => {
    if (window.google) {
        resolve(window.google);
        initializeAutocomplete;
    } else {
        const script = document.createElement("script");
        script.src = `https://maps.googleapis.com/maps/api/js?key=${google_api_key}&libraries=places,marker`;
        script.async = true;
        script.onload = () => resolve(window.google);
        script.onerror = (error) => reject(error);
        script.onload = initializeAutocomplete;
        document.head.appendChild(script);
    }
    });
};

onMounted(() => {
    loadGoogleMapsAPI().catch((error) => {
        console.error("Google Maps API yüklenirken hata oluştu:", error);
    });

    Fancybox.bind('[data-fancybox]', {
    //
    });
});


</script>